import classes from "./Stats.module.css";
import axios from "axios";
import React, { PureComponent } from "react";
import { PieChart, Pie, Sector, Cell } from "recharts";
const { stats, paragraph } = classes;

const data01 = [
  { name: "Group A", value: 400 },
  { name: "Group B", value: 300 },
  { name: "Group C", value: 300 },
  { name: "Group D", value: 200 },
];
const data02 = [
  { name: "A1", value: 100 },
  { name: "A2", value: 300 },
  { name: "B1", value: 100 },
  { name: "B2", value: 80 },
  { name: "B3", value: 40 },
  { name: "B4", value: 30 },
  { name: "B5", value: 50 },
  { name: "C1", value: 100 },
  { name: "C2", value: 200 },
  { name: "D1", value: 150 },
  { name: "D2", value: 50 },
];

export default class Example extends PureComponent {
  state = { total: 0 };
  static jsfiddleUrl = "https://jsfiddle.net/alidingling/w6wsrc52/";

  render() {
    return (
      <div className={stats}>
        <p className={paragraph}>
          Hier komen uiteindelijk de statistieken. Maar dan moeten er wel eerst een
          aanzienlijk aantal de enquete hebben ingevuld. Op dit moment zijn dat
          er: <strong>{this.state.total}</strong>
        </p>
        <PieChart width={400} height={400}>
          <Pie
            data={data01}
            dataKey="value"
            cx={200}
            cy={200}
            outerRadius={60}
            fill="#8884d8"
          />
          <Pie
            data={data02}
            dataKey="value"
            cx={200}
            cy={200}
            innerRadius={70}
            outerRadius={90}
            fill="#82ca9d"
            label
          />
        </PieChart>
      </div>
    );
  }
  componentDidMount() {
    const apiCall = async () => {
      try {
        const req = await axios.get("/polldata");
        console.log(req.data.numberOfDataSets);
        const total = req.data.numberOfDataSets;
        this.setState({ total });
      } catch (error) {
        console.log(error);
      }
    };
    apiCall();
  }
}
