import React from "react";
import validator from "validator";
import { useState } from "react";
import axios from "axios";
import { BrowserRouter as Router, Route, Link, Switch } from "react-router-dom";

import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";

import { Container } from "react-bootstrap";

import Navbar from "./components/UI/Navbar";
import Content from "./components/Content";
import Stats from "./components/Stats";
import About from "./components/About";

function App() {
  const [stage, setStage] = useState(1);
  const [answers, setAnswers] = useState([false, -1, -1, -1, -1, 0, 0, 0, 0]);
  const [email, setEmail] = useState("");
  const [alertMessage, setAlertMessage] = useState(null);
  const [alertColor, setAlertColor] = useState();
  const [about, setAbout] = useState(false);

  const clicked = (e) => {
    //setting first answer to true (when pressed start):

    if (e.target.getAttribute("name") === "about") {
      e.preventDefault();
      setAbout(true);
    }

    if (
      e.target.getAttribute("name") === "backdrop" ||
      e.target.getAttribute("name") === "x"
    ) {
      setAbout(false);
    }

    if (stage === 1) {
      const toBeUpatedAnswers = [...answers];
      toBeUpatedAnswers[0] = true;
      setAnswers(toBeUpatedAnswers);
    }

    //buttons functionality:

    const eventName = e.target.getAttribute("name");
    if (eventName === "start") {
      setStage(stage + 1);
    } else if (eventName === "volgende") {
      if (answers[stage - 1] === -1) {
        setAlertColor("red");
        setAlertMessage("Kies alsjeblieft een antwoord...");
        // alleen in development:
        // setStage(stage + 1);
      } else {
        setAlertMessage(null);
        setStage(stage + 1);
      }
    } else if (eventName === "vorige") {
      setAlertMessage("");
      setStage(stage - 1);
    } else if (eventName === "verstuur") {
      if (!email || !validator.isEmail(email)) {
        setAlertColor("red");
        setAlertMessage("Geef alsjeblieft een geldig email adres...");
        return;
      }

      const fetchData = async () => {
        const pollData = await axios.post("/polldata", {
          pollData: answers,
          email,
          date: new Date().toISOString().slice(0, 10),
        });
        if (pollData.status === 200) {
          setStage(stage + 1);
          setAlertColor("green");
          setAlertMessage(pollData.data.msg);
        } else if (pollData.status === 201) {
          setAlertColor("red");
          setAlertMessage(pollData.data.msg);
        }
      };
      fetchData();
    }
  };
  const changed = (e) => {
    //multiple choice:

    if (stage === 2 || stage === 4 || stage === 5) {
      const name = e.target.name;
      const value = parseInt(e.target.value);
      const toBeUpatedAnswers = [...answers];
      toBeUpatedAnswers[name] = value;
      setAnswers(toBeUpatedAnswers);
    }

    //a <select> question:

    if (stage === 3) {
      const name = e.target.name;
      const value = parseInt(e.target.value);
      const toBeUpatedAnswers = [...answers];
      toBeUpatedAnswers[name] = value;
      setAnswers(toBeUpatedAnswers);
    }
    // submitting email:
    if (stage === 9) {
      setEmail(e.target.value);
    }
  };

  return (
    <Router>
      <Switch>
        <Container>
          {about ? <About clicked={clicked} /> : null}
          <Route exact path={"/"}>
            <Navbar
              link={"Bekijk de statistieken"}
              route="/stats"
              clicked={clicked}
            />
            <Content
              alertMessage={alertMessage}
              alertColor={alertColor}
              length={answers.length}
              stage={stage - 1}
              answers={answers}
              clicked={clicked}
              changed={changed}
              email={email}
            />
          </Route>
          <Route exact path={"/stats"}>
            <Navbar
              clicked={clicked}
              link={"Terug naar de enquette"}
              route=""
            />
            <Stats />
          </Route>
        </Container>
      </Switch>
    </Router>
  );
}

export default App;
